import { mapMutations, mapState } from "vuex";
import Scroll from "../scroll/scroll";

export default {
  name: "person",
  data() {
    return {
      pulldown: false,
      data_person: [],
      isReadOnly: true,
      btn: "",
      age: null,
      sex: null,
      email: null,
      phone: null
    };
  },
  methods: {
    back() {
      this.$router.back();
      this.back_show(false);
    },
    ...mapMutations(["back_show"]),
    set_readonly() {
      if (this.btn == "") {
        this.$dialog.confirm({
          title: "",
          mes: "",
          opts: [
            {
              txt: "",
              color: false,
              callback: () => {
                this.isReadOnly = false;
                this.btn = "";
                this.set_readonly();
              }
            },
            {
              txt: "",
              color: true,
              callback: () => {
                let email_re = /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/;
                let phone_re = /^[1][3,4,5,7,8][0-9]{9}$/;
                if (email_re.test(this.email) == false) {
                  this.$dialog.toast({
                    mes: "",
                    timeout: 1500
                  });
                } else if (phone_re.test(this.phone) == false) {
                  this.$dialog.toast({
                    mes: "",
                    timeout: 1500
                  });
                } else if (this.age == "") {
                  this.$dialog.toast({
                    mes: "",
                    timeout: 1500
                  });
                } else {
                  this.$http
                    .post("/personUpdate", {
                      user: this.user,
                      age: this.age,
                      sex: this.sex,
                      email: this.email,
                      phone: this.phone
                    })
                    .then(
                      response => {
                        if (response.body.flag == true) {
                          this.$dialog.toast({
                            mes: "",
                            timeout: 1500
                          });
                          this.isReadOnly = true;
                          this.btn = "";
                        }
                      },
                      error => {
                        console.log(error);
                      }
                    );
                }
              }
            }
          ]
        });
      } else {
        this.isReadOnly = !this.isReadOnly;
        this.btn = this.isReadOnly == true ? "" : "";
      }
    }
  },
  components: {
    Scroll
  },
  computed: {
    ...mapState(["user"])
  },
  created() {
    this.$http
      .post("person", {
        user: this.user
      })
      .then(
        response => {
          this.data_person = response.body;
          this.age = response.body[0].age;
          this.sex = response.body[0].sex;
          this.email = response.body[0].email;
          this.phone = response.body[0].phone;
        },
        error => {
          console.log("false");
        }
      );
  }
};



