import BScroll from "better-scroll";

export default {
  props: {
    /**
     * 1 scroll
     * 2 scroll
     * 3 scrollswipescroll
     */
    probeType: {
      type: Number,
      default: 1
    },
    /**
     * click
     */
    click: {
      type: Boolean,
      default: true
    },
    /**
     * 
     */
    scrollX: {
      type: Boolean,
      default: false
    },
    /**
     * 
     */
    listenScroll: {
      type: Boolean,
      default: false
    },
    /**
     * 
     */
    data: {
      type: Array,
      default: null
    },
    /**
     * 
     */
    pullup: {
      type: Boolean,
      default: false
    },
    /**
     * 
     */
    pulldown: {
      type: Boolean,
      default: false
    },
    /**
     * 
     */
    beforeScroll: {
      type: Boolean,
      default: false
    },
    /**
     * scroll
     */
    refreshDelay: {
      type: Number,
      default: 200
    }
  },
  mounted() {
    // DOMbetter-scroll
    // setTimeout(() => {
    //   this._initScroll()
    // }, 20)
    this.$nextTick(() => {
      setTimeout(() => {
        this._initScroll();
      }, 200);
    });
  },
  updated() {
    this.$nextTick(() => {
      setTimeout(() => {
        this._initScroll();
      }, 200);
    });
  },
  methods: {
    _initScroll() {
      if (!this.$refs.wrapper) {
        return;
      }
      // better-scroll
      if (!this.scroll) {
        this.scroll = new BScroll(this.$refs.wrapper, {
          probeType: this.probeType,
          click: this.click,
          scrollX: this.scrollX
        });
        // console.log(this.scroll);
      } else {
        this.scroll.refresh();
        // console.log(this.scroll);
      }

      //
      let img = this.$refs.wrapper.getElementsByTagName("img");
      let count = 0;
      let length = img.length;
      if (length) {
        let timer = setInterval(() => {
          if (count == length) {
            this.scroll.refresh(); //bs
            clearInterval(timer);
          } else if (img[count].complete) {
            count++;
          }
        }, 50);
      }

      // 
      if (this.listenScroll) {
        this.scroll.on("scroll", pos => {
          this.$emit("scroll", pos);
        });
      }

      // 
      if (this.pullup) {
        this.scroll.on("scrollEnd", () => {
          // 
          if (this.scroll.y <= this.scroll.maxScrollY + 50) {
            this.$emit("scrollToEnd");
          }
        });
      }

      // 
      if (this.pulldown) {
        this.scroll.on("touchend", pos => {
          // 
          if (pos.y > 50) {
            this.$emit("pulldown");
          }
        });
      }

      // 
      if (this.beforeScroll) {
        this.scroll.on("beforeScrollStart", () => {
          this.$emit("beforeScroll");
        });
      }
    },
    disable() {
      // better-scrolldisable
      this.scroll && this.scroll.disable();
    },
    enable() {
      // better-scrollenable
      this.scroll && this.scroll.enable();
    },
    refresh() {
      // better-scrollrefresh
      this.scroll && this.scroll.refresh();
    },
    scrollTo() {
      // better-scrollscrollTo
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments);
    },
    scrollToElement() {
      // better-scrollscrollToElement
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments);
    }
  },
  watch: {
    // refreshDelayrefresh
    data() {
      setTimeout(() => {
        this.refresh();
      }, this.refreshDelay);
    }
  }
};



